import axios from "axios";
import { UserModel } from "../models/UserModel";
import { toast } from "react-toastify";


class AuthServices {
    public async rgisterNewUser(newUser: UserModel): Promise<UserModel> {
        try {
            const respone = await axios.post<UserModel>('http://localhost:3001/api/auth/register', newUser);
            const token = respone.data;
            return token;
        } catch (error) {
            console.log(error);
            throw error;
        }
    }

    public async loginUser(email: string, password: string): Promise<string> {
        try {
            const respone = await axios.post('http://localhost:3001/api/auth/login', { email, password });
            const token = respone.data;
            return token;
        } catch (e: any) {
            if (e?.response?.data?.error) { toast.error(e.response.data.error); }
            return "Error";
        }

    }



}


const authServices = new AuthServices();
export default authServices;