import { useState } from "react";
import { Button, InputGroup, Modal } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useSelector } from "react-redux";
import { State } from "../../../../app/store";
import { UserModel, UserRole } from "../../../../models/UserModel";
import { VacationModel } from "../../../../models/VacationModel";
import adminServices from "../../../../services/adminServices";
import vacationsServices from "../../../../services/vacationsServices";
import "./VacationCard.css";


function VacationCard({ id, destination, description, startDate, endDate, price, totalLikes, userLikes, photoName, func }: VacationModel | any): JSX.Element {

    const userDetials = useSelector<State, UserModel>((state) => state.auth);
    const [isFilled, setIsFilled] = useState(userLikes);
    const [countLikes, setCountLikes] = useState(totalLikes);
    const { register, handleSubmit, formState: { errors } } = useForm();


    ///Bootstrap Modal
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    const handleClick = () => {
        setIsFilled(!isFilled);
        if (isFilled !== true) {
            vacationsServices.userAddLike(userDetials.id as number, id as number);
            let updateCounLikes = countLikes + 1;
            setCountLikes(updateCounLikes);

        } else {
            vacationsServices.userRmoveLike(userDetials.id as number, id as number);
            let updateCounLikes = countLikes - 1;
            setCountLikes(updateCounLikes);
        }
    }

    const deleteVaction = (id: number | any) => {
        console.log(id);
        adminServices.deleteVaction(id);
        func(id);
    }

    const onSubmit = (data: VacationModel | any) => {
        const photoInput = document.getElementById("photoName") as HTMLInputElement;
		const photo: any = photoInput.files?.[0];
        adminServices.editVacation({ id, ...data }, photo).then(() => {
            func(id);
            handleClose();
        })
    };

    ///helper function to set deafult value on dates
    function formatDate(date: Date | string): string {
        if (!date) return "";
        const d = new Date(date);
        const year = d.getFullYear();
        const month = ("0" + (d.getMonth() + 1)).slice(-2);
        const day = ("0" + d.getDate()).slice(-2);
        return `${year}-${month}-${day}`;
    };


    return (
        <div className="card vacationCard" key={id}>
            <div className="imgContainer">
                <img src={`${photoName}?${new Date().getTime()}`} alt={destination} />
            </div>
            <div className="tittleAndLikes">
                <h3 className="tittle">{destination}</h3>
                {userDetials.role === UserRole.ADMIN ?
                    <div className="editVacation" >
                        <InputGroup >
                            <Button className="custom-edit-button" onClick={handleShow}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="15" height="16" fill="white" className="bi bi-pencil-fill" viewBox="0 0 16 16">
                                    <path d="M12.854.146a.5.5 0 0 0-.707 0L10.5 1.793 14.207 5.5l1.647-1.646a.5.5 0 0 0 0-.708l-3-3zm.646 6.061L9.793 2.5 3.293 9H3.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.207l6.5-6.5zm-7.468 7.468A.5.5 0 0 1 6 13.5V13h-.5a.5.5 0 0 1-.5-.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.5-.5V10h-.5a.499.499 0 0 1-.175-.032l-.179.178a.5.5 0 0 0-.11.168l-2 5a.5.5 0 0 0 .65.65l5-2a.5.5 0 0 0 .168-.11l.178-.178z" />
                                </svg>
                            </Button>
                            <Button className="custom-delete-button" onClick={() => deleteVaction(id)}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="15" height="16" fill="currentColor" className="bi bi-trash3-fill" viewBox="0 0 16 16" >
                                    <path d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5Zm-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5ZM4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06Zm6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528ZM8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5Z" />
                                </svg>
                            </Button>
                        </InputGroup>

                    </div>
                    :
                    <div className="likesArea">
                        <svg xmlns="http://www.w3.org/2000/svg" width="28" height="23" fill={isFilled ? '#F7D876' : 'white'} className="bi bi-heart-fill" viewBox="0 0 16 16" onClick={handleClick}>
                            <path fillRule="evenodd" d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z" />
                        </svg>
                        <p className="totalLikesText">{countLikes} Likes</p>
                    </div>
                }

                <Modal
                    show={show}
                    onHide={handleClose}
                    backdrop="static"
                    keyboard={false}
                    className="vacationModal"
                >


                    <Modal.Header closeButton>
                        <Modal.Title>Edit Vacation</Modal.Title>
                    </Modal.Header>
                    <Modal.Body >
                        <form action="" className="modalBody">

                            <label htmlFor="">Dstination</label>
                            <input type="text" {...register("destination", { required: true })} defaultValue={destination} />
                            {errors.destination && <p className="errorMessage">Destination is required.</p>}

                            <label htmlFor="">Description</label>
                            <input type="text" {...register("description", { required: true })} defaultValue={description} />
                            {errors.description && <p className="errorMessage" >Description is required.</p>}

                            <label htmlFor="">Start Date</label>
                            <input type="date" {...register("startDate", { required: true })} defaultValue={formatDate(startDate)} />

                            <label htmlFor="">End Date</label>
                            <input type="date" {...register("endDate", {
                                required: true,
                                validate: (value) => !startDate || value >= startDate,
                            })} defaultValue={formatDate(endDate)} />
                            {errors.endDate && <p className="errorMessage">End Date is invalid or earlier than Start Date.</p>}


                            <label htmlFor="">Price</label>
                            <input placeholder="$" type="number" {...register("price", {
                                required: true,
                                min: 0,
                                max: 10000,
                            })} defaultValue={price} />
                            {errors.price && <p className="errorMessage">Price must be between 0 and 10000.</p>}


                            <label htmlFor="">Cover Image</label>
                            <input type="file" accept=".png, .jpg, .jpeg" id="photoName" {...register("photoName")} />

                        </form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Cancel
                        </Button>
                        <Button variant="warning" className="updateVacationBtn" onClick={handleSubmit(onSubmit)}>Update</Button>
                    </Modal.Footer>
                </Modal>

            </div>

            <div className="date">
                <div >{startDate.split('T')[0]}</div>
                &nbsp; &nbsp;
                <div >{endDate.split('T')[0]}</div>
            </div>
            <p className="description">{description} </p>
            <div className="vacationPrice">
                {price}$
            </div>
        </div>


    );
}

export default VacationCard;
