import axios from "axios";
import { ReportModel } from "../models/ReportModel";
import { VacationModel } from "../models/VacationModel";



class AdminServices {

    public async getReports(): Promise<ReportModel[]> {
        try{
            const respone = await axios.get<ReportModel[]>('http://localhost:3001/api/reports');
            return respone.data;
        }catch(error){
            console.log(error);
            throw error;
        }
    }

    public async addVacation(newVacation: VacationModel, photo: File): Promise<VacationModel> {
        try{
            const formData = new FormData();
            formData.append("photo", photo);
            formData.append("vacation", JSON.stringify(newVacation));
            
            const respone = await axios.post<VacationModel>("http://localhost:3001/api/vacations", formData, {
                headers: { "Content-Type": "multipart/form-data" },
            })
            
            // Update the newVacation object with the returned data.
            newVacation.id = respone.data.id;
            newVacation.photoName = respone.data.photoName;
            return respone.data;
        }catch(error){
            console.log(error);
            throw error;
        }
    }

    public async deleteVaction(id: number) {
        try{
            const respone = await axios.delete(`http://localhost:3001/api/vacations/${id}`);
            return respone.data;
        }catch(error){
            console.log(error);
            throw error;
        }
    }

    public async editVacation(updatedVacation: VacationModel, photo: File) {
        try{
            const formData = new FormData();
            formData.append("photo", photo);
            formData.append("vacation", JSON.stringify(updatedVacation));
            const respone = await axios.patch(`http://localhost:3001/api/vacations/${updatedVacation.id}`, formData, {
                headers: { "Content-Type": "multipart/form-data" },
            });
            return respone.data;
        }catch(error){
            console.log(error);
            throw error;
        }
    }

}

const adminServices = new AdminServices();
export default adminServices;
