import axios from "axios";
import { LikeModel } from "../models/LikeModel";
import { VacationModel } from "../models/VacationModel";

class VacationsServices {



    public async getAllVacations(page: number): Promise<VacationModel[]> {
    try{
        const respone = await axios.get<VacationModel[]>(`http://localhost:3001/api/vacations?page=${page}`, { headers: { Authorization: `Barrer ${window.localStorage.getItem('token')}` } });
        const vacations = respone.data;
        // Add the photo URL to each vacation
        vacations.forEach(vacation => {
            vacation.photoName = `http://localhost:3001/assets/${vacation.id}.png`;
        });
        
        return vacations;
    }catch(error){
        console.log(error);
        throw error;
    }
    }

    public async userAddLike(userId: number, vacationId: number): Promise<LikeModel> {
        try{
            const respone = await axios.post<LikeModel>(`http://localhost:3001/api/vacations/addLike`, { userId: userId, vacationId: vacationId }, { headers: { Authorization: `Barrer ${window.localStorage.getItem('token')}` } });
            return respone.data;
        }catch(error){
            console.log(error);
            throw error
        }
    }
    public async userRmoveLike(userId: number, vacationId: number): Promise<LikeModel> {
        try{
            const respone = await axios.post<LikeModel>(`http://localhost:3001/api/vacations/removeLike`, { userId: userId, vacationId: vacationId }, { headers: { Authorization: `Barrer ${window.localStorage.getItem('token')}` } });
            return respone.data;
        }catch(error){
            console.log(error);
            throw error;
        }
    }



    public async getVacationsByType(page: number, userId: number, type:string ): Promise<VacationModel[]> {
       try{
           const response = await axios.get<VacationModel[]>(`http://localhost:3001/api/vacations/${type}?page=${page}`, { headers: { Authorization: `Bearer ${window.localStorage.getItem('token')}` }, params: { userId } });
           const vacations = response.data;
           // Add the photo URL to each vacation
           vacations.forEach(vacation => {
               vacation.photoName = `http://localhost:3001/assets/${vacation.id}.png`;
            });
            return vacations;
        }catch(error){
            console.log(error);
            throw error;
        }
    }
    


}

const vacationsServices = new VacationsServices();
export default vacationsServices;
