import VacationCard from "./VacationCard/VacationCard";
import "./Content.css";
import { VacationModel } from "../../../models/VacationModel";
import { useEffect, useState } from "react";
import vacationsServices from "../../../services/vacationsServices";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { Button, InputGroup, Modal } from "react-bootstrap";
import { useSelector } from "react-redux";
import { UserModel, UserRole } from "../../../models/UserModel";
import { State } from "../../../app/store";
import { useForm } from "react-hook-form";
import adminServices from "../../../services/adminServices";

function Content(): JSX.Element {
	const userDetials = useSelector<State, UserModel>((state) => state.auth);
	const { register, handleSubmit, reset, formState: { errors }, watch } = useForm();

	const [vacations, setVacations] = useState<VacationModel[]>([]);
	const [vacationsAfterDelete, setVacationsAfterDelete] = useState<VacationModel[]>([]);
	const [currentPage, setCurrentPage] = useState(1);
	const [totalCards, setTotalCards] = useState(10);
	const [newVacation, setNewVacation] = useState<VacationModel | null>(null);
	const navigate = useNavigate();

	///Fillters states
	const [currentVacationType, setCurrentVacationType] = useState('')

	///Bootstrap Modal
	const [show, setShow] = useState(false);
	const handleClose = () => {
		setShow(false);
		reset();
	};
	const handleShow = () => setShow(true);


	useEffect(() => {
		if (currentVacationType === 'likes-vacations') {
			showVacationByCorrectType('likes-vacations');
		} else if (currentVacationType === 'active-vacations') {
			showVacationByCorrectType('active-vacations');
		} else if (currentVacationType === 'future-vacations') {
			showVacationByCorrectType('future-vacations');
		}
		else if (currentVacationType === '') {
			vacationsServices.getAllVacations(currentPage).then(res => setVacations(res));
		}
	}, [newVacation, vacationsAfterDelete]);


	const startDate = watch('startDate');

	function showVacationByCorrectType(vacationFillterType: string) {
		setCurrentPage(1);
		vacationsServices.getVacationsByType(currentPage, userDetials.id as number, vacationFillterType).then(res => {
			setVacations(res);
			setTotalCards(res.length);
		})
	}


	const checkIfUserLike = (userLikes: any) => {
		if (userLikes !== null) {
			return true;
		} else {
			return false;
		}
	}

	const handlePageChange = (pageDirection: string) => {
		let newPage = 0;
		if (pageDirection === "next") {
			newPage = currentPage + 1;
		} else {
			newPage = currentPage - 1;
		}

		if (currentVacationType !== '') {
			vacationsServices.getVacationsByType(newPage, userDetials.id as number, currentVacationType).then(res => {
				setVacations(res);
				setTotalCards(res.length);
				setCurrentPage(newPage);
			});
		} else {
			vacationsServices.getAllVacations(newPage).then(res => {
				setVacations(res);
				setTotalCards(res.length);
				setCurrentPage(newPage);
			});
		}
	};


	const hndleLogout = () => {
		window.localStorage.removeItem('token');
		navigate('/login');
	}
	const handleClearFilters = () => {
		setCurrentVacationType("");
		setCurrentPage(1);
		vacationsServices.getAllVacations(1).then(res => setVacations(res));
		setTotalCards(10);

	}

	const onSubmit = (data: VacationModel | any) => {
		const photoInput = document.getElementById("photoName") as HTMLInputElement;
		const photo: any = photoInput.files?.[0];


		if (photo) {
			adminServices.addVacation(data, photo)
				.then(vacation => {
					setNewVacation(vacation);
					handleClose();
				});
		} else {
			console.error("Photo is not selected");
			// Show an error message if the photo is not selected
		}
	};

	const pull_data = (data: any, correctVactions: VacationModel[]) => {
		correctVactions = vacations;
		const index = correctVactions.findIndex(correctVactions => correctVactions.id === data);
		if (index >= 0) {
			correctVactions.splice(index, 1);
		};
		setVacationsAfterDelete(correctVactions)
	}




	if (!userDetials.id) {
		return <Navigate to="/login" state={{ from: window.location.origin }} replace />;
	}

	return (

		<div className="Content">
			<h1 className="logo">Tripwise</h1>

			<div className="filtersArea">
				<Button onClick={hndleLogout} variant="dark" className="logoutBtn">
				<span> Logout</span>	
					<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-power" viewBox="0 0 16 16">
						<path d="M7.5 1v7h1V1h-1z" />
						<path d="M3 8.812a4.999 4.999 0 0 1 2.578-4.375l-.485-.874A6 6 0 1 0 11 3.616l-.501.865A5 5 0 1 1 3 8.812z" />
					</svg>
				</Button>

				{/* ADMIN filters menu */}
				{userDetials.role === UserRole.ADMIN ?
					<InputGroup>
						<Link to="/reports"> <Button variant="dark">Watch reports</Button></Link>
						<Button variant="warning" onClick={handleShow}>Add new vacation</Button>

					</InputGroup>
					:
					<InputGroup className="mb-3">
						{(currentVacationType) && <Button variant="danger" onClick={handleClearFilters}>Clear</Button>}

						<Button variant="dark" onClick={() => { showVacationByCorrectType('likes-vacations'); setCurrentVacationType('likes-vacations') }} disabled={currentVacationType === 'likes-vacations' || currentVacationType === '' ? false : true} >Likes <span className="extraTextFilter">vacations</span> </Button>
						<Button variant="dark" onClick={() => { showVacationByCorrectType('future-vacations'); setCurrentVacationType('future-vacations') }} disabled={currentVacationType === 'future-vacations' || currentVacationType === '' ? false : true}>Future <span className="extraTextFilter">vacations</span> </Button>
						<Button variant="dark" onClick={() => { showVacationByCorrectType('active-vacations'); setCurrentVacationType('active-vacations') }} disabled={currentVacationType === 'active-vacations' || currentVacationType === '' ? false : true}>Active <span className="extraTextFilter">vacations</span> </Button>
					</InputGroup>
				}

			</div>


			{/* Add New Vacation Model */}

			<Modal
				show={show}
				onHide={handleClose}
				backdrop="static"
				keyboard={false}
				className="vacationModal"
			>
				<Modal.Header closeButton>
					<Modal.Title>Add Vacation</Modal.Title>
				</Modal.Header>
				<Modal.Body >
					<form action="" className="modalBody">

						<label htmlFor="">Dstination</label>
						<input type="text" {...register("destination", { required: true })} />
						{errors.destination && <p className="errorMessage">Destination is required.</p>}

						<label htmlFor="">Description</label>
						<input type="text" {...register("description", { required: true })} />
						{errors.description && <p className="errorMessage">Description is required.</p>}

						<label htmlFor="">Start Date</label>
						<input type="date" {...register("startDate", {
							required: true,
							min: new Date().toISOString().split("T")[0],
						})} />
						{errors.startDate && <p className="errorMessage">Start Date is invalid or in the past.</p>}

						<label htmlFor="">End Date</label>
						<input type="date" {...register("endDate", {
							required: true,
							validate: (value) => !startDate || value >= startDate,
						})} />
						{errors.endDate && <p className="errorMessage">End Date is invalid or earlier than Start Date.</p>}


						<label htmlFor="">Price</label>
						<input placeholder="$" type="number" {...register("price", {
							required: true,
							min: 0,
							max: 10000,
						})} />
						{errors.price && <p className="errorMessage">Price must be between 0 and 10000.</p>}

						<label htmlFor="">Cover Image</label>
						<input type="file" accept=".png, .jpg, .jpeg" id="photoName" {...register("photoName", {
							required: true
						})} />
						{errors.photoName && <p className="errorMessage">Must add photo.</p>}

					</form>
				</Modal.Body>
				<Modal.Footer>
					<Button variant="secondary" onClick={handleClose}>
						Cancel
					</Button>
					<Button className="addVacationBtn" variant="warning" onClick={handleSubmit(onSubmit)}>Add Vacation</Button>
				</Modal.Footer>
			</Modal>



			<div className="vocationContainer">
				{vacations?.map((vacation) =>
					<VacationCard key={vacation.id} id={vacation.id} destination={vacation.destination} description={vacation.description} startDate={vacation.startDate} endDate={vacation.endDate} price={vacation.price} totalLikes={vacation.totalLikes} userLikes={checkIfUserLike(vacation.userLikes)} photoName={vacation.photoName} func={pull_data} />
				)}
			</div>
			<div className="paginationArea">
				<button className="prevButton" onClick={() => handlePageChange("prev")} disabled={currentPage === 1}>Prev</button>
				page {currentPage} (total {totalCards})
				<button className="nextButton" onClick={() => handlePageChange("next")} disabled={totalCards < 10}>Next</button>
			</div>

		</div>
	);
}

export default Content;
