import { Button, Form } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { AuthState, login } from "../../app/authSlice";
import authServices from "../../services/authService";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "./Login.css";

function Login(): JSX.Element {
    const { register, handleSubmit, formState: { errors } } = useForm<AuthState>();
    const dispatch = useDispatch();
    const navigate = useNavigate();

    // async function loginUser({ email, password }: AuthState) {
    //     try {
    //       const token = await authServices.loginUser(email, password);
    //       if (token) {
    //         toast.success('Login successful', 'success');
    //         dispatch(login(token));
    //         navigate('/');
    //       } else {
    //         showToast('Login failed. Please try again', 'error');
    //       }
    //     } catch (error) {
    //       showToast('An error occurred. Please try again', 'error');
    //     }
    //   }
      
    async function loginUser({ email, password }: AuthState) {

            const token = await authServices.loginUser(email, password);
            dispatch(login(token));
            navigate('/');
    }

    

    return (
        <div className="Login">
            <ToastContainer />
            <h1 className="mainLogo">Tripwise</h1>

            <div className="loginContainer" >
                <h1>Login</h1>
                <Form className="loginForm" onSubmit={handleSubmit(loginUser)}>
                    <Form.Control type="email" placeholder="Enter email" required {...register("email", { required: true })} />
                    <Form.Control type="password" placeholder="Enter password" required {...register("password", { required: true })} />
                    {errors.password && <p className="errorMessage">Password must have min of 4 chars.</p>}

                    <div className="registerArea">
                        <p>Not a member yet ? </p>
                        <Link to="/register" >Register now</Link>

                    </div>
                    <Button type="submit" >Login</Button>
                </Form>

            </div>
        </div>
    );
}

export default Login;
