import { useState } from "react";
import { Button, Form } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { login } from "../../app/authSlice";
import { UserModel } from "../../models/UserModel";
import authServices from "../../services/authService";
import "./Register.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Register(): JSX.Element {
    const { register, handleSubmit, formState: { errors } } = useForm<UserModel>();
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const [errorMessage, setErrorMessage] = useState("");

    async function addNewUser(newUser: UserModel) {
        try {
            const token = await authServices.rgisterNewUser(newUser);
            dispatch(login(String(token)));
            navigate('/');
        } catch (error: any) {
            setErrorMessage(error.response.data.error);
            toast.error(errorMessage);
            console.log(error);
        }
    };



    return (
        <div className="Register">
            <ToastContainer />

            <h1 className="mainLogo">Tripwise</h1>

            <div className="registerContainer">
                <h1 >Register</h1>
                <Form className="registerForm" onSubmit={handleSubmit(addNewUser)}>
                    <Form.Control type="text" placeholder="Enter firstname" {...register("firstname", { required: true })} />
                    {errors.firstname && <p className="registerError">Firsst name is required.</p>}

                    <Form.Control type="text" placeholder="Enter last name" {...register("lastName", { required: true })} />
                    {errors.lastName && <p className="registerError">Last name is required.</p>}

                    <Form.Control type="email" placeholder="Enter email" required {...register("email", { required: true })} />
                    <Form.Control type="password" placeholder="Enter password" {...register("password", { required: true, min: 4 })} />
                    {errors.password && <p className="registerError">Password has to have min 4 chars.</p>}

                    <div className="registerArea">
                        <p>Already have acount ? </p>
                        <Link to="/login" >Login now</Link>

                    </div>
                    <Button type="submit">Register</Button>
                </Form>

            </div>

        </div>
    );
}

export default Register;
